* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.profile {
  width: 360px;
  height: 310px;
  border: 1px solid silver;
  flex-direction: column;
  display: flex;
}

.profile__header {
  height: 110px;
  background-color: #cceafb;
  justify-content: center;
  align-items: center;
  display: flex;
}

.profile__avatar {
  width: 80px;
  height: 80px;
  background-color: #5fbbef;
  border: 1px solid #000;
  border-radius: 50%;
  margin: 0 15px;
}

.profile__header-info {
  height: 80px;
  flex-direction: column;
  flex-grow: 1;
  justify-content: space-between;
  font-size: 16px;
  font-weight: 700;
  display: flex;
}

.profile__header-additional-info {
  color: #717171;
  flex-direction: column;
  font-size: 14px;
  font-weight: 300;
  line-height: 20px;
  display: flex;
}

.profile-content {
  flex-direction: column;
  flex-grow: 1;
  justify-content: space-evenly;
  align-items: center;
  display: flex;
}

.profile-content-item {
  height: 36px;
  font-size: 16px;
  line-height: 36px;
  display: flex;
}

.profile-content-item__name {
  width: 90px;
  color: #717171;
  border: 1px solid #717171;
  border-radius: 4px 4px 0;
  padding-left: 9px;
  display: inline-block;
}

.profile-content-item__value {
  width: 200px;
  color: #000;
  border: 1px solid #717171;
  border-radius: 4px 4px 4px 0;
  margin-left: -1px;
  padding-left: 14px;
  font-size: 16px;
  display: inline-block;
}

.profile-footer {
  height: 65px;
  display: flex;
}

.profile__button-edit {
  text-align: center;
  text-transform: uppercase;
  color: #717171;
  background-color: silver;
  border: none;
  flex-grow: 1;
  font-family: Exo2, sans-serif;
  font-size: 42px;
  font-weight: 700;
  line-height: 65px;
}

.profile__button-edit:hover {
  color: #cceafb;
  background-color: #5fbbef;
}

.profile__button-exit-outer {
  background-color: #717171;
}

.profile__button-exit-outer:hover {
  background-color: #bf1616;
}

.profile__button-exit {
  width: 65px;
  height: 100%;
  background: url("../../button-exit.479d236f.svg") 50% no-repeat;
  border: none;
}

.profile__button-exit:hover {
  background: url("../../button-exit-hover.725fb86e.svg") 50% no-repeat;
}

